import React, { useState } from "react";
import ReactDOM from "react-dom";
import { WidthProvider, Responsive } from "react-grid-layout";
import Chart from '../Chart/Chart';
import AddInput from './AddInput';
import _ from "lodash";
import Image from 'react-bootstrap/Image';
import { ReactSession } from 'react-client-session';



const ResponsiveReactGridLayout = WidthProvider(Responsive);


/**
 * This layout demonstrates how to use a grid with a dynamic number of elements.
 */
class DragAndDrop extends React.PureComponent {
    static defaultProps = {
        className: "layout",
        cols: { lg: 12, md: 12, sm: 6, xs: 6, xxs: 6 },
        rowHeight: 100
    };

    selectModal = (info) => {
        this.setState({ modal: !this.state.modal })
    }

    constructor(props) {
        super(props);
        
        this.state = {
            items: [0].map(function (i, key, list) {
                const w = _.random(6, 6);
                const h = _.random(4, 4);
                return {
                    x: (i * 6) % 9,
                    y: Math.floor(i / 1),
                    w: w,
                    h: h,
                    i: i.toString(),
                    add: i === (list.length - 1)
                };
            }),
            newCounter: 0
        };

        this.onAddItem = this.onAddItem.bind(this);
        this.onBreakpointChange = this.onBreakpointChange.bind(this);
        this.onLayoutChange = this.onLayoutChange.bind(this);
      //  this.onRemoveItem = this.onRemoveItem.bind(this);
        //this.state = {
        //    currentLayout: layouts.lg || [],
        //    layouts
        //};

       // const layouts = cookies.get(onLayoutChange) || {};
      //  console.log("Get" + onLayoutChange);
    }

    createElement(el) {
        const removeStyle = {
            position: "absolute",
            right: "2px",
            top: 0,
            cursor: "pointer"
        };
        const i = el.add ? "+" : el.i;
        return (
            <div key={i} data-grid={el}>
                {el.add ? (
                    <span className="text">
                        <Chart />
                    </span>
                )
                    : (
                        i == 0 ? (
                            <div className="w-100">
                                <span className="text"><Chart /></span>
                            </div>)
                    : (<span class="text"></span>)
                    )
                    }
                    <span className="remove mdi mdi-18 mdi-close w-25 text-end" style={removeStyle} onClick={this.onRemoveItem.bind(this, i)}> </span>
                </div>
        );
    }

    onAddItem() {
        /*eslint no-console: 0*/
        console.log("adding", "n" + this.state.newCounter);
        this.setState({
            // Add a new item. It must have a unique key!
            items: this.state.items.concat({
                i: "n" + this.state.newCounter,
                x: (this.state.items.length * 1) % (this.state.cols || 12),
                y: Infinity, // puts it at the bottom
                w: 12,
                h: 4
            }),
            // Increment the counter to ensure key is always unique.
            newCounter: this.state.newCounter + 1
        });
    }

    // We're using the cols coming back from this to calculate where to add new items.
    onBreakpointChange(breakpoint, cols) {
        this.setState({
            breakpoint: breakpoint,
            cols: cols
        });
    }

    onLayoutChange(layout) {
      //  this.props.onLayoutChange(layout);
        this.setState({ layout: layout });
    }

    onRemoveItem(i) {
        console.log("removing", i);
        this.setState({ items: _.reject(this.state.items, { i: i }) });
    }
    refreshData = (data) => {
        console.log(data);
        this.onAddItem();
    };
    render() {
        return (
            <div className="row m-0">
                 <div className="col-12 d-lg-inline-flex pe-0">
                    <ResponsiveReactGridLayout className="col-12" onLayoutChange={this.onLayoutChange} onBreakpointChange={this.onBreakpointChange} {...this.props} >
                        {_.map(this.state.items, el => this.createElement(el))}
                    </ResponsiveReactGridLayout>
                </div>
            </div>
        );
    }
}

if (process.env.STATIC_EXAMPLES === true) {
    import("./AddGridLayout.js").then(fn => fn.default(DragAndDrop));
}

export default DragAndDrop;
