import React, { useState } from "react";
import ReactDOM from "react-dom";
import Image from 'react-bootstrap/Image';

function AddInput() {
    const [isOpen, setIsOpen] = useState(false);
    const handleTrigger = () => setIsOpen(!isOpen);
  
    return (
      <div className="App">
        <div className="page">
            <div className="content">
                
            </div>

            <div className={`sidebar ${isOpen ? "sidebar--open" : ""}`}>
                <div className="trigger" onClick={handleTrigger}>
                    <div className="form-group text-start mx-2">
                        <label class>Set X Value</label>
                        <input type="text" className="form-control mb-3" />
                    </div>
                    <div className="form-group text-start me-4">
                        <label class>Set Y Value</label>
                        <input type="text" className="form-control mb-3" />
                    </div>
                    <Image src="/DragAndDrop/images/toggleMenu.svg" className="img-fluid ms-2" />
                    <Image src="/DragAndDrop/images/DeleteItem.svg" className="img-fluid mx-1" />
                </div>
            </div>
        </div>
      </div>
    );
  }

  export default AddInput;